@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding:50px;
  background:url(/static/media/bg.340111c4.png) no-repeat center center;
  background-size:cover;
  font-family: 'DM Sans', sans-serif;
}


.box {
  position:relative;
  display:inline-block;
  background: #FFFFFF;  
  box-shadow: 0px 0px 16px rgba(58, 78, 88, 0.05);
  border-radius: 12px;
  padding:15px 30px;
  box-sizing:border-box;
  margin:25px 0px;
}

.box.full {
  width:100%;
}


.box.small {
  width:100%;
  max-width:375px;
  margin-left:auto;
  text-align:center;
}

.box.small.dominance-market {
  min-height:720px;
}

.dashboard__numbers {
  display:flex;
  flex-wrap:wrap;
}
.dashboard__numbers .column {
  padding-right:5px;
  padding-bottom:5px;
}
.dashboard__numbers button {
  background:transparent;
  border:2px solid #ccc;
  border-radius:16px;
  padding:5px 10px;
  cursor:pointer;
  white-space:nowrap;
}
.select-dropdown {
  min-width:200px;
  display:block;
  position: absolute !important;
  right: 30px;
  top: 29px;
}

.select-dropdown + .dailydot {
  padding-top:25px;
}
.container {
  display:block;
  padding:15px 25px;
  max-width:1250px;
  margin:0px auto;
}

.meter {
  border-radius:4px;
  display:block;
  width:100%;
  height:23px;
  background:#f8f8f8;
  max-width:850px;
  position:relative;
  min-width:12px;
}

.meter > span {
  background: #0C3EE3;
  height:100%;
  display:block;
  width:auto;
  position:absolute;
  border-radius:4px 0px 0px 4px;

}
.meter > span + span {
  background:#f6cc27;
  border-radius:0px 4px 4px 0px;
}
.meter + div {
  width:225px;
  padding-left:10px;
}

.icon {
  max-width:50px;
  width:100%;
  padding:10px;
}

.flex {
  display:flex;
  align-items: center;
  flex-direction: row;

}

h3 {
  color:#222;
}

.tvl.auctions {
  height:auto ;
  min-height:486px;
  margin-right:0px;
}

.auctions p {
  padding:10px 0px;
  margin:0px;
}

.tvl h3 {
  padding-bottom:0px;
  margin-bottom:0px;
}
.network-header {
  background: #f8f8f8;
  padding: 0px 28px;
  margin: 0px -30px;
  line-height:0px;
  margin-top:15px;
}
.auctions .network {
  border-bottom:1px dashed #f2f2f2;
}
.auctions .network:last-of-type {
  border-bottom:0px;
}

.network-name {
  width:137px;
  position:relative;
}

.network-name .icon {
  display:none;
} 

.network-cap, .network-auction , .network-total{
  width:90px;
  text-align:right;
}

.auctions {
  min-width:60%;
  margin-left:auto;
  margin-right:0px;
}

.selectors span {
  padding-right:35px;
  line-height:15px;
}


.lockedTVL {

  min-height:220px;
}

.winner {
  background: rgba(29, 166, 59, 0.15);
  color: #1da63b;
  border-radius: 39px;
  padding: 2px 7px;
  height: 15px;
  font-size:11px;
  position:absolute;
  right:-25px;
  top:2px;
}


.lockedTVL .recharts-tooltip-wrapper small, .porcentshare small {
  font-size:50%;
  color:#ccc;
}

.lockedTVL .recharts-tooltip-wrapper {
  background:#fff;
  position:absolute !important;
  top:-52px !important;
  left:0px !important;
  transform:none !important;
  list-style-type:none;
  width:100%;  
}

.left {
  text-align:left;
  margin-bottom:0px;
}
.selectors span i {
  display:inline-block;
  width:16px;
  height:16px;
  background:#F0F3FB;
  border-radius:4px;
  vertical-align:middle;
  vertical-align:-2px;
  margin-right:5px;
}

.selectors .blue {  
  background:#0C3EE3;
}
.selectors .yellow {
  background:#f6cc27;
}

.flex.small {
  min-width:150px;
  color:#5E6D7C;
  font-size:16px;
  padding:10px;
  box-sizing:border-box;
}

.icon img {
  width:100%;
  max-width:40px;
  margin:0px auto;

}

.tvl .recharts-responsive-container {
  margin-top:25px;
}

.justify {
  justify-content:space-between;
}

.score {
  max-width:250px;
}

.score-circle {
  stroke: #0C3EE3;
  transition: stroke-dashoffset 1s ease-out;
}

.js-circle {
  stroke-linecap: round;

}

h3 span {
  float:right;
  font-weight:normal;
  font-size:15px;
}

.score-empty {
  stroke: #f8f8f8;
}



.score-text {
  fill: #1e1935;
  font-size:60px;
  font-weight:bold;
}
.score-text.small {
  fill:#5E6D7C;
  font-size:25px;
}

.score-text.big {
  font-size:125px;
}

.dominance canvas{
  margin:25px auto;
  max-width:250px;
  height:auto !important;
}

.variation {
  padding-left:10px;
  padding-bottom:6px;
  display:inline-block;
  font-size:75%;
  vertical-align:middle;
}

.variation.positive{ 
  color:#1DA63B;  
}

.variation.negative {
  color:#f22c31;  
}

.giant {
  font-size: 40px;
  font-weight: 700;
  margin: 0;
  width:100%;
  padding-top: 0px;
  text-align:left;
  
}
.giant + .recharts-responsive-container {
  margin-top:0px;
  max-height:220px;
}

p.big {
  font-size: 35px;
  font-weight: bold;
  margin:0px;
  vertical-align:middle;
}

.tvl {
  margin-right:50px;
  width:100%;
  max-width:825px;
  height:350px;
}

.dominance-market {
  margin-right:50px;
  text-align:left !important;
}

.dominance img {
  width:25px;
  left:0px;
  position:absolute;
}

.dominance > div {
  position:relative;
  padding-left:50px;
}

.crowdloantvl {
  margin:50px 0px;
}

.tvl-tooltip {
  list-style-type:none;
  background:rgba(248,248,248,0.95);
  padding:5px 15px;
  border-radius:8px;
}
.tvl-tooltip p {
  padding:5px !important;
  margin:0px;
}


.crowdloan-by{
  display:flex;
  align-items:center;
}

.contribution-size div {
  height:42px;
}

.flex.column {
  flex-direction:column;
}

.flex.column .flex {
  width:100%;
}

.flex.column .flex > div {
  min-width:150px;
}



[data-tooltip] {
  position: relative;
  z-index: 2;
  cursor: pointer;
}

/* Hide the tooltip content by default */
[data-tooltip]:before,
[data-tooltip]:after {
  visibility: hidden;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  pointer-events: none;
}

/* Position tooltip above the element */
[data-tooltip]:before {
  position: absolute;
  bottom: 105%;
  left: 50%;
  margin-bottom: 5px;
  margin-left: -30px;
  padding: 6px;
  width: 50px;
  border-radius: 3px;
  background-color: #000;
  background-color: hsla(0, 0%, 20%, 0.9);
  color: #fff;
  content: attr(data-tooltip);
  text-align: center;
  font-size: 13px;
  line-height: 1.2;
}

/* Triangle hack to make tooltip look like a speech bubble */
[data-tooltip]:after {
  position: absolute;
  bottom: 105%;
  left: 50%;
  margin-left: -5px;
  width: 0;
  border-top: 5px solid #000;
  border-top: 5px solid hsla(0, 0%, 20%, 0.9);
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  content: " ";
  font-size: 0;
  line-height: 0;
}

/* Show tooltip content on hover */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
  visibility: visible;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.daily-dots {
  min-height:300px;
}

.distribution > .column {
  margin-top:25px;
}

.distribution > .column > div {
  padding:5px 0px;
}


.chart-dominance {
  position:relative;
  padding-left:0px !important;
}

.chart-dominance .score-text {
  position:absolute;
  left:50%;
  top:50%;
  transform:translate(-50%,-50%)
}

.chart-dominance .recharts-wrapper {
  margin:0px auto;
}


.chart-dominance .score-text{
  padding-top:15px;
  fill: #1e1935;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    line-height: 25px;
}
.chart-dominance .score-text small {
  color:#ccc;
  font-size:70%;
}

.half {
  max-width:48%;
  width:100%;
}

.ml-25 {
  margin-right:2%;
}
.mr-25 {
  margin-left:2%;
}

@media only screen and (max-width: 1200px)  {
  .globalTvl, .dailydot {
    margin:0px -25px;
  }
  body {
    padding:50px 25px;
  }
  p.big {
    //font-size:50px;
  }
  p.giant {
    font-size:50px;
  }
}

@media only screen and (max-width: 980px)  {
  .half {
    max-width:100%;
    width:100%;
  }
  
  .ml-25 {
    margin-right:0%;
  }
  .mr-25 {
    margin-left:0%;
  }
  .dominance .recharts-wrapper {
    padding-left:0px;
  }

  .meter {
    max-width:100%;
  }
  .box {
    margin:15px 0px;
  }
  .flex {
    flex-wrap: wrap;
  }
  .tvl {
    height:auto;
  }
  p.big, p.giant {
    padding-top:0px;
    padding-bottom:10px;
    width:100%;
    text-align:center;
    position:relative;
    top:auto;
    left:auto;
    transform:none;
  }
  .flex > div {
    
      flex:1 0 100%;
      max-width:100% !important;
  }
  .network-name .icon {
    display:block;
  } 
  .network-name span {
    display:none;
  }
  .network-name {
    width:50px;
    flex:auto !important;
  }
}



@media only screen and (max-width: 765px)  { 
  body {
    padding:25px 15px;
  }
  .select-dropdown {
    position:relative !important;
    top:auto;
    right:auto;
  }
  .container {
    padding:0px;
  }
  p.big, p.giant {
    font-size:30px;
    text-align:left;
  }
  .meter {
    max-width:100%;
  }

  .full > div {
    text-align:center;
  }
  .full > div .flex {
    position:relative;
    margin-bottom:25px;
  }
  .icon {
    padding:0px;
    display:block;
    text-align:left;
  }
  .meter + div {
    position: absolute;
    right: 0px;
    top: 0px;
    padding-right:0px !important;
    text-align:right;
  }
  .network-name {
    width:50px;
    flex:auto !important;
  }
  .network-name .icon {
    display:block;
  } 
  .network-name span {
    display:none;
  }
  .network-header {
    flex-wrap: initial;
    margin-bottom:25px;
  }

  .network-header h4 {
    font-size:13px;
  }
  .auctions h3 span {
    display:block;
    width:100%;
    text-align:left;
    padding-bottom:25px;
  }
  .network-cap, .network-auction, .network-total {
    width:60px;
  }
  .network-auction {
    width:75px;
  }
  .selectors span {
    display:block;
    text-align:left;
    margin-bottom:15px;
  }
  .selectors span:last-of-type {
    margin-bottom:50px;
  }
  .giant + .recharts-responsive-container {
    min-height:200px;
  }
  .box.small.dominance-market {
    min-height:auto;
  }


  
}
